import React from "react";
import "react-awesome-slider/dist/styles.css";
import AwesomeSlider from "react-awesome-slider";
import "./Slider.css";
import withAutoplay from "react-awesome-slider/dist/autoplay";
import imageBack from "../images/OIP.jpg";
import imgOne from "../images/mini player and todo app/music_mini_player .png";
import imgTwo from "../images/mini player and todo app/Listen Peaceful Music.png";
import imgThree from "../images/mini player and todo app/Todo_app_one.png";
import imgFour from "../images/mini player and todo app/Todo_app_two.png";

const deliveryData = [
  {
    image: imgOne,
  },
  {
    image: imgTwo,
  },
  {
    image: imgThree,
  },
  {
    image: imgFour,
  },
];
const AutoplaySlider = withAutoplay(AwesomeSlider);

const SliderEight = () => {
  return (
    <>
      <div className=" w-[50vw] h-[40vh] slidebox">
        <div
          id="background_slider"
          className="w-full mt-2 md:my-4 m-auto md:mt-20"
        >
          <h1
            className="uppercase text-green-400	font-semibold text-2xl text-center my-4"
            id="categoriesTitle"
          >
            Mini music player And Todo App (javascript)
          </h1>

          <div className="flex justify-evenly items-center  ">
            <a href="https://pasupathirock.github.io/mini-music-player-javascript-/">
              {" "}
              <button className="bg-yellow-300 p-1 rounded-lg cursor-pointer ">
                Project 1 Demo{" "}
              </button>
            </a>
            <a href="pasupathirock.github.io/todo_javascript/">
              {" "}
              <button className="bg-yellow-300 p-1 rounded-lg cursor-pointer ">
                Project 2 Demo{" "}
              </button>
            </a>
          </div>

          <div className="w-full relative mt-2 mb-2">
            <img
              src={imageBack}
              className="w-screen "
              alt="delivery background"
            />

            <AutoplaySlider
              className="deliverySlider awssld__content"
              mobileTouch={true}
              play={true}
              cancelOnInteraction={false} 
              interval={5000}
            >
              {deliveryData.map((slide, index) => {
                return (
                  <div key={index}>
                    <img
                      src={slide.image}
                      className="w-[100%]  m-auto md:w-[100%]"
                      alt="slider"
                    />
                  </div>
                );
              })}
            </AutoplaySlider>
          </div>
        </div>
      </div>
    </>
  );
};

export default SliderEight;
