import React, { useState } from "react";
import image from "../images/PortFolio_img.jpeg";
import { Link, Switch, Route, Redirect } from "react-router-dom";
import Resume from "./pages/Resume";
import MyProjects from "./pages/MyProjects";
import ContactMe from "./pages/ContactMe";
import AboutMe from "./pages/AboutMe";

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className="container w-screen">
      <div className="flex justify-between items-center sticky top-0 z-10 bg-opacity-80 bg-black p-4">
        <div className="flex items-center">
          <img
            className="rounded-full object-cover w-16 h-16"
            src={image}
            alt="sample"
          />
          <h4 className="ml-4 text-white font-bold">
            Hi I am Pasupathi
          </h4>
        </div>
        <div className="block lg:hidden">
          <button
            onClick={toggleMenu}
            className="text-white focus:outline-none"
          >
            <svg
              className="w-6 h-6"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d={isOpen ? "M6 18L18 6M6 6l12 12" : "M4 6h16M4 12h16M4 18h16"}
              ></path>
            </svg>
          </button>
        </div>
        <div
          className={`${
            isOpen ? "block" : "hidden"
          } lg:flex lg:flex-row lg:items-center w-1/4 lg:w-auto absolute lg:static top-4 right-0 bg-black bg-opacity-70  lg:bg-transparent z-20 p-4 lg:p-0`}
        >
          <button
            onClick={toggleMenu}
            className="block lg:hidden ml-auto text-white"
          >
            <svg
              className="w-6 h-6"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M6 18L18 6M6 6l12 12"
              ></path>
            </svg>
          </button>
          <div className="flex flex-col lg:flex-row lg:items-center w-full lg:w-auto lg:ml-8 space-y-4 lg:space-y-0 lg:space-x-4">
            <Link
              className="block text-center lg:text-left text-white text-lg hover:scale-110 transition duration-300 ease-in-out p-1 rounded-lg hover:text-orange-500"
              exact
              to="/"
            >
              Resume
            </Link>
            <Link
              className="block text-center lg:text-left text-white text-lg hover:scale-110 transition duration-300 ease-in-out p-1 rounded-lg hover:text-red-500"
              to="/MyProjects"
            >
              Projects
            </Link>
            <Link
              className="block text-center lg:text-left text-white text-lg hover:scale-110 transition duration-300 ease-in-out p-1 rounded-lg hover:text-green-500"
              to="/about"
            >
              About
            </Link>
            <Link
              className="block text-center lg:text-left text-white text-lg hover:scale-110 transition duration-300 ease-in-out p-1 rounded-lg hover:text-blue-500"
              to="/contact"
            >
              Contact Me
            </Link>
          </div>
        </div>
      </div>
      <Switch>
        <Route exact path="/" component={Resume}></Route>
        <Route path="/MyProjects" component={MyProjects}></Route>
        <Route path="/about" component={AboutMe}></Route>
        <Route path="/contact" component={ContactMe}></Route>
      </Switch>
      <Redirect to="/" />
    </div>
  );
};

export default Navbar;
